.footer {
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  a {
    text-decoration: none;
    color: $white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding: 5px 15px;
    border-radius: 4px;
    font-size: 16px;
  }

  .github {
    background: $github-bg;
  }
  .twitter {
    background: $twitter-bg;
  }
  .youtube {
    background: $youtube-bg;
  }
}
